@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

.font-hero-nextep {
    font-size: 24px;
    font-family: "Lato", sans-serif;
    font-weight: 700;
}

.font-hero-description-nextep {
    font-size: 12px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}

.font-button-nextep {
    font-size: 16px;
    font-family: "Lato", sans-serif;
    font-weight: 400;
}

.font-menu-item-nextep {
    font-size: 12px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
}

.font-section-title-nextep {
    font-size: 16px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
}

.font-content-nextep {
    font-size: 16px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}
  
@media(min-width: 640px) {
    .font-hero-nextep {
        font-size: 44px;
        font-family: "Lato", sans-serif;
        font-weight: 700;
    }

    .font-hero-description-nextep {
        font-size: 20px;
        font-family: "Roboto", sans-serif;
        font-weight: 400;
    }

    .font-menu-item-nextep {
        font-size: 14px;
        font-family: "Roboto", sans-serif;
        font-weight: 500;
    }

    .font-section-title-nextep {
        font-size: 24px;
        font-family: "Roboto", sans-serif;
        font-weight: 500;
    }

    .font-content-nextep {
        font-size: 20px;
        font-family: "Roboto", sans-serif;
        font-weight: 400;
    }

    .font-button-nextep {
        font-size: 16px;
        font-family: "Lato", sans-serif;
        font-weight: 400;
    }
}

  